n    border-radius: 27px;\r\n    padding: 8px 13px;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    overflow-x: auto;\r\n    -ms-overflow-style: none; /* IE and Edge */\r\n    scrollbar-width: none; /* Firefox */\r\n}\r\n.guest-invi-navbar::-webkit-scrollbar {\r\n    display: none;\r\n}\r\n.guest-invi-navbar > a {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 15px;\r\n    line-height: 22px;\r\n    text-align: center;\r\n    color: #9a9a9a;\r\n    padding: 8px 40px;\r\n    cursor: pointer;\r\n    white-space: nowrap;\r\n    text-decoration: none;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.guest-invi-navbar > a > span {\r\n    width: 46px;\r\n    height: 46px;\r\n    margin-bottom: 25px;\r\n    border-radius: 50%;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    background: rgba(255, 255, 255, 0.1);\r\n}\r\n.guest-invi-navbar > .active {\r\n    background: #ffffff;\r\n    border-radius: 23px;\r\n    color: #461952;\r\n    font-weight: 700;\r\n}\r\n/* Navbar css end */\r\n@media screen and (max-width: 768px) {\r\n    .guest-invi-navbar-cont {\r\n        top: 63px;\r\n    }\r\n    .guest-invi-navbar {\r\n        width: 100%;\r\n        max-width: 100% !important;\r\n        border-radius: 0 !important;\r\n    }\r\n    .guest-invi-navbar > .active {\r\n        background: inherit;\r\n        border-radius: 0;\r\n        color: #ffffff;\r\n        font-weight: 700;\r\n    }\r\n    .guest-invi-navbar > .active > span {\r\n        background-color: #ffffff;\r\n    }\r\n}\r\n@media screen and (max-width: 576px) {\r\n    .guest-invi-navbar-cont {\r\n        top: 56px;\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
